body {
  margin: 0;
  padding: 0;
  overflow-x: hidden;
}

.app {
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
